export function hamburgerClickHandler (event, popUpElement) {
    if(popUpElement.style.top === '0px') {
        popUpElement.style.top = "100%"

    }else{
        popUpElement.style.top = 0
    }
}

export function closeButtonPopUp (event, popUpElement) {
    popUpElement.style.top = "100%"
}

export function gamePageClickHandler (event, game, startPage, gameIndex, birdsMenu, popUp) {
    game.style.opacity = "1"
    startPage.style.opacity = "0"
    gameIndex.style.zIndex = "1"
    birdsMenu.style.display = "block"
    if(popUp.style.top === '0px') {
        popUp.style.top = "100%"

    }
}

export function startPageClickHandler (event, game, startPage, gameIndex, birdsMenu, popUp) {
    game.style.opacity = "0"
    startPage.style.opacity = "1"
    gameIndex.style.zIndex = "-1"
    birdsMenu.style.display = "none"
    if(popUp.style.top === '0px') {
        popUp.style.top = "100%"

    } 
}