import './index.html';
import './index.scss';
import {hamburgerClickHandler} from './modules/header';
import {closeButtonPopUp} from './modules/header';
import {startPageClickHandler} from './modules/header';
import {gamePageClickHandler} from './modules/header';
import {playButtonHandler, getRandomNumber} from './modules/start-page';
import {birdsData} from './modules/birds-lib';


const headerLogo = document.querySelector('.header__logo'); 
const humburger = document.querySelector('.header__humburger'); 
const popUp = document.querySelector('.pop-up');
const closeBtnPopUp = document.querySelector('.btn-close');
const game = document.querySelector('.game');
const startPage = document.querySelector('.main');
const gameIndex = document.querySelector('.game-page');
const startPageBtn = document.querySelector('.link-forStartPage');
const gamePageBtn = document.querySelector('.link-forGamePage');
const playBtn = document.querySelector('.btn-play');
const birdsMenu = document.querySelector('.birds-menu-wrapper');
const unknownImg = document.querySelector('.unknown-image');
const validImg = document.querySelector('.valid-image');
const activeImg = document.querySelector('.active');
const instruction = document.querySelector('.instruction');
const gameInfo = document.querySelector('.game__info');
const btnForNextGame = document.querySelector('.next-round-btn');
const playerInfoAudioWrapper = document.querySelector('.player__info__input');

const quiz = document.getElementById('quiz')
const item = document.getElementById('item')
const player = document.getElementById('player')


humburger.addEventListener('click', (event) => hamburgerClickHandler(event, popUp))

closeBtnPopUp.addEventListener('click', (event) => closeButtonPopUp(event, popUp))

startPageBtn.addEventListener('click', (event) => startPageClickHandler(event, game, startPage, gameIndex, birdsMenu, popUp))

headerLogo.addEventListener('click', (event) => startPageClickHandler(event, game, startPage, gameIndex, birdsMenu, popUp))

gamePageBtn.addEventListener('click', (event) => gamePageClickHandler(event, game, startPage, gameIndex, birdsMenu, popUp))






const birdsPackIndex = getRandomNumber(birdsData.length)
const birdsPack = birdsData[birdsPackIndex]
const guesingBirdIndex = getRandomNumber(birdsPack.length)
const guesingBird = birdsData[birdsPackIndex][guesingBirdIndex]

//  
playBtn.addEventListener('click', (event) => {
    playButtonHandler(event, game, startPage, gameIndex, birdsMenu, popUp)
    
    renderAnswers(birdsPack);
    renderPlayer(guesingBird);
    renderPlayerInfoChoose();
    
})
// !!!!!!!!!!!!!!     
btnForNextGame.addEventListener('click', () => {
    // const birdsPackIndex = getRandomNumber(birdsData.length)
    // const birdsPack = birdsData[birdsPackIndex]
    // const guesingBirdIndex = getRandomNumber(birdsPack.length)
    // const guesingBird = birdsData[birdsPackIndex][guesingBirdIndex]

    renderAnswers(birdsPack);
    renderPlayer(guesingBird);
    renderPlayerInfoChoose();
    btnForNextGame.style.opacity = "0"
    btnForNextGame.style.zIndex = "-1"
})

//    
const getWonName = (wonName) => {
    return String(wonName.name)
};

quiz.addEventListener('click', (event) => {
    //  
    
    if (event.target.classList.contains('menu__item')){
        
        const itemText = (event.target.innerText);
        const trueStyle = event.target;
        const falseStyle = event.target;
        renderAnswersClickHandler(getWonName(guesingBird), String(itemText), trueStyle, falseStyle)
        openHiddenCard()
        
        
    }
})

//        (audio)
const renderPlayer = (birdItem) => {
    
    player.innerHTML = `
    <div class="player__img">
                <img src=${birdItem.image} alt="bird-logo" class="valid-image">
                <img src="https://cdn.pixabay.com/photo/2017/02/13/01/26/the-question-mark-2061539_1280.png" alt="question" class="unknown-image">
              </div>
              <div class="player__info">
                <div class="player__info__descr">* * *</div>
                <div class="line black"></div>
                <div class="player__info__input">
                  <audio controls="controls">
                    <source src=${birdItem.audio} type="audio/mpeg">
                  </audio>
                </div>
              </div>
    `;
};

//     
const renderPlayerAfterChoose = (birdItem) => {
    
    player.innerHTML = `
    <div class="player__img">
                <img src=${birdItem.image} alt="bird-logo" class="valid-image">
                <img src=${birdItem.image} alt="question" class="unknown-image">
              </div>
              <div class="player__info">
                <div class="player__info__descr">${birdItem.name}</div>
                <div class="line black"></div>
                <div class="player__info__input">
                  <audio controls="controls">
                    <source src=${birdItem.audio} type="audio/mpeg">
                  </audio>
                </div>
              </div>
    `;
};
//   item
const renderAnswer = (birdItem) => {
    return `<div class="menu__item">${birdItem.name}</div>`
};
//  item   quiz
const renderAnswers = (birdsPack) => {
    const resultArr = [];
        birdsPack.forEach((el) => {
        resultArr.push(renderAnswer(el));
    })
    quiz.innerHTML = resultArr.join('');
};
//     
const renderAnswersClickHandler = (wonName, itemName, trueStyle, falseStyle) => {
    if(wonName === itemName) {
        trueStyle.classList.add("true-btn");
        renderPlayerAfterChoose(guesingBird)
        addNextBtn();
        renderPlayerInfoAfterChoose(guesingBird);
    } else {
        falseStyle.classList.add("false-btn");
    }
};

//    
const openHiddenCard = () => {
    instruction.style.display = "none"
    gameInfo.style.display = "flex"
}

const renderPlayerInfoChoose = (birdItem) => {
    gameInfo.innerHTML = `
    <div class="score">Your score : 0</div>
            <div class="line"></div>
            
            <div class="game__info__player">
              <div class="game__info__player__img ">
                <img src="/src/img/bird-logo-1.png" alt="bird-logo" class="valid-image">
                <img src="https://cdn.pixabay.com/photo/2017/02/13/01/26/the-question-mark-2061539_1280.png" alt="question" class="unknown-image active">
              </div>
              <div class="game__info__player__info">
                <div class="game__info__player__info__descr">* * *</div>
                <div class="info-line"></div>
                <div class="game__info__player__info__text">
                  * * *
                </div>
              </div>
            </div>
            <div class="player-info"> 
              
            </div>

            <div class="line"></div>
            
            <div class="game__info__description">
            </div>
    `;
};

const renderPlayerInfoAfterChoose = (birdItem) => {
    gameInfo.innerHTML = `
    <div class="score">Your score : 0</div>
            <div class="line"></div>
            
            <div class="game__info__player">
              <div class="game__info__player__img ">
                <img src=${birdItem.image} alt="bird-logo" class="valid-image">
                <img src=${birdItem.image} alt="question" class="unknown-image active">
              </div>
              <div class="game__info__player__info">
                <div class="game__info__player__info__descr">${birdItem.name}</div>
                <div class="info-line"></div>
                <div class="game__info__player__info__text">
                ${birdItem.species}
                </div>
              </div>
            </div>
            <div class="player-info">
              <audio controls="controls">
                <source src=${birdItem.audio} type="audio/mpeg">
              </audio>
            </div>

            <div class="line"></div>
            
            <div class="game__info__description">
            ${birdItem.description}
            </div>
    `;
};

//      
const addNextBtn = () => {
    btnForNextGame.style.opacity = "1"
    btnForNextGame.style.zIndex = "1"
}




